<template>
    <p-card class="p-mt-2">
        <template #content>
            <p-divider align="left" class="p-mt-0">
                <b>Pregnancy Outcome</b>
            </p-divider>

            <div class="p-grid p-fluid">
                <div class="p-col-fixed p-mt-2">
                    <label for="">Pregnancy / Birth outcome</label>
                </div>
                <div class="p-col-fixed">
                    <p-dropdown
                        v-model="form.pregnancyBirthOutcome"
                        :options="pregnancyBirthOutcomeOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div class="p-col-fixed p-mt-2">
                    <label for="">Newborn date of birth</label>
                </div>
                <div class="p-col-fixed">
                    <p-calendar
                        v-model="form.newbornDateBirth"
                        :showIcon="true"
                        dateFormat="yy-mm-dd"
                    />
                </div>
                <div class="p-col-fixed p-mt-2">
                    <label for="">Pregnancy Loss Date</label>
                </div>
                <div class="p-col-fixed">
                    <p-calendar
                        v-model="form.pregnancyLossDate"
                        :showIcon="true"
                        dateFormat="yy-mm-dd"
                    />
                </div>
            </div>

            <p-divider align="left" class="p-mt-0">
                <b>Babies Born</b>
            </p-divider>

            <div class="p-grid p-fluid">
                <div class="p-col-fixed p-mt-2">
                    <label for=""># babies born</label>
                </div>
                <div class="p-col-fixed">
                    <p-input-text v-model="form.babiesborn" />
                </div>
                <div class="p-col-fixed p-mt-2">
                    <label for="">Gestational Age</label>
                </div>
                <div class="p-col-fixed">
                    <p-input-text v-model="form.gestationalWeeks" />
                </div>
                <div class="p-col-fixed p-mt-2">
                    <label for="">Weeks</label>
                </div>
                <div class="p-col-fixed">
                    <p-input-text v-model="form.gestationalDays" />
                </div>
                <div class="p-col-fixed p-mt-2">
                    <label for="">Days</label>
                </div>
            </div>
        </template>

        <template #footer>
            <p-button
                icon="pi pi-save"
                label="Save"
                @click="save"
                :loading="loading"
            />
        </template>
    </p-card>
</template>

<script>
import { usePregnancyOutcomeForm } from "@/composable/born";
import { onMounted, watch } from "vue-demi";
export default {
    setup() {
        const {
            store,
            form,
            fillForm,
            save,
            loading,
            pregnancyBirthOutcomeOptions
        } = usePregnancyOutcomeForm();

        watch(() => store.state.born.pregnancyOutcome, fillForm);

        onMounted(() => {
            if (store.state.born.pregnancyOutcome == null) {
                store.dispatch(
                    "born/getBronV2",
                    store.state.artCycle.selectedCycle
                );
            } else {
                fillForm(store.state.born.pregnancyOutcome);
            }
        });

        return { form, pregnancyBirthOutcomeOptions, save, loading };
    }
};
</script>

<style scoped>
.p-calendar {
    height: 24.5px;
}
</style>
